"use client"

import { useEffect, useRef } from "react"

export default function FlashlightEffect() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const mouseRef = useRef({ x: -9999, y: -9999 }) // hide initially

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext("2d")
    if (!ctx) return

    const resize = () => {
      canvas.width = window.innerWidth
      canvas.height = window.innerHeight
    }

    resize()
    window.addEventListener("resize", resize)

    const handleMouseMove = (e: MouseEvent) => {
      mouseRef.current = { x: e.clientX, y: e.clientY }
    }

    window.addEventListener("mousemove", handleMouseMove)

    const drawFlashlight = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height)

      // Full black background (very subtle overlay)
      ctx.fillStyle = "rgba(0,0,0,1)"
      ctx.fillRect(0, 0, canvas.width, canvas.height)

      const { x, y } = mouseRef.current

      // Core + glow radiuses
      const coreRadius = 140
      const glowRadius = 320
      const vignetteRadius = 1200

      // BRIGHT CENTER
      const core = ctx.createRadialGradient(x, y, 0, x, y, coreRadius)
      core.addColorStop(0, "rgba(255,255,255,0.85)")
      core.addColorStop(1, "rgba(255,255,255,0)")
      ctx.fillStyle = core
      ctx.beginPath()
      ctx.arc(x, y, coreRadius, 0, Math.PI * 2)
      ctx.fill()

      // GLOW HALO
      const glow = ctx.createRadialGradient(x, y, coreRadius, x, y, glowRadius)
      glow.addColorStop(0, "rgba(255,255,255,0.22)")
      glow.addColorStop(1, "rgba(0,0,0,0)")
      ctx.fillStyle = glow
      ctx.beginPath()
      ctx.arc(x, y, glowRadius, 0, Math.PI * 2)
      ctx.fill()

      // FOG BLOOM
      ctx.globalAlpha = 0.12
      ctx.beginPath()
      ctx.arc(x, y, glowRadius * 1.45, 0, Math.PI * 2)
      ctx.fillStyle = "white"
      ctx.fill()
      ctx.globalAlpha = 1

      // VIGNETTE
      const vignette = ctx.createRadialGradient(
        canvas.width / 2,
        canvas.height / 2,
        0,
        canvas.width / 2,
        canvas.height / 2,
        vignetteRadius
      )
      vignette.addColorStop(0, "rgba(0,0,0,0)")
      vignette.addColorStop(1, "rgba(0,0,0,0.65)")
      ctx.fillStyle = vignette
      ctx.fillRect(0, 0, canvas.width, canvas.height)
    }

    const loop = () => {
      drawFlashlight()
      requestAnimationFrame(loop)
    }

    loop()

    return () => {
      window.removeEventListener("mousemove", handleMouseMove)
      window.removeEventListener("resize", resize)
    }
  }, [])

  return (
    <canvas
      ref={canvasRef}
      className="pointer-events-none fixed inset-0 z-[1] opacity-40"
    />
  )
}
